import React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {getStatus, setUsersProfile, updateStatus} from "../../Redax/profile-reducer";
import {withRouter} from "react-router-dom";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";

class ProfileContainer extends React.Component {
    componentDidMount() {
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.autoraizedUserId;
            if (!userId) {
                this.props.history.push('/Login');
            }
        }
        this.props.setUsersProfile(userId)
        this.props.getStatus(userId)
    }

    render() {

        return (
            <Profile {...this.props} />
        )
    }
}

let mapStateToProps = (state) => ({
    profile: state.profilePage.profile,
    status: state.profilePage.status,
    autoraizedUserId: state.auth.userId,
    isAuth: state.auth.isAuth
})

export default compose(
    connect(mapStateToProps, {setUsersProfile, getStatus, updateStatus}),
    withRouter,
     withAuthRedirect)
(ProfileContainer);